

import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import CheckIcon from 'vux/src/components/check-icon/index.vue'

export default {
props: ['selected', 'list', 'type'],
data () {
return {
select: -1,
last: -1
}
},
watch: {
selected (newV, oldV) {
this.select = newV
}
},
methods: {
onClick (itemValue, itemDisabled) {
if (this.select === itemValue) {
return
}
this.select = itemValue
this.$emit('on-select', itemValue)
},
showVideo (index) {
if (this.last !== -1) {
this.$refs['video' + this.last][0].pause()
}
this.$refs['video' + index][0].play()
this.last = index
},
previewImage (url) {
this.$wechat.previewImage({current: url, urls: [url]})
}
},
components: {
Checker,
CheckerItem,
CheckIcon
}
}



